#harry {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 5%; /* max 95, min 5 */
    left: 15%; /* max 85, min 15 */
    width: 19vw;
    height: 4vh;
    display: block;
    font-family: 'Technor';
    font-weight: bold;
    font-size: 3vh;
    text-align: center;
    color: #76d61c;
    cursor: pointer;
    transform: translate(-50%, -50%);
    transition-duration: 400ms;
    transition-property: opacity;
    opacity: 0;
    z-index: 10;
}

#harry.rigth {
    opacity: 1;
}


/* Celular */
@media (orientation: landscape) {
    #harry {
        font-size:5vh;
        height: 6vh;
    }
}