.lds-ring {
    display: block;
    position: absolute;
    left: 50%; top: 50%;
    width: 1em;
    height: 1em;
    font-size: 40px;
    transform: translate(-50%,-50%);
    transition: opacity 150ms;
    opacity: 0;
}

.lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 0.8em;
    height: 0.8em;
    margin: 0.1em;
    border: 0.1em solid #9037AF;
    border-radius: 50%;
    animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #9037AF transparent transparent transparent;
}

.loading .lds-ring {
    opacity: 1;
}

.lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
}

@keyframes lds-ring {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}