@import url(styles/fonts.css);
@import url(styles/side.css);
@import url(styles/buttons.css);
@import url(styles/lettering.css);
@import url(styles/questions.css);
@import url(styles/loading.css);

* {
  box-sizing: border-box;
}

div,
span,
a {
  user-select: none;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #9037AF;
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #612774;
}

body {
  margin: 0;
  background-color: #9037AF;
  font-family: 'Larsseit';
  font-weight: normal;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root,
body,
html {
  height: 100%;
}

a,
a:hover,
a:visited {
  cursor: pointer;
  color: #fff;
  text-decoration: none;
}

p {
  text-align: justify;
  text-justify: inter-word;
}

h2 {
  font-family: 'Technor';
  font-weight: bold;
  font-size: 3em;
}

h3 {
  font-weight: normal;
}

h4 {
  font-size: 26px;
  margin: 0;
}


/* Celular */
@media (orientation: portrait) {
  .logo {
    width: 150px;
  }

  h2 {
    font-size: 2em;
  }
}

/* PC / TABLET */
@media (orientation: landscape) {
  .logo {
    width: 200px;
  }
}

.archive::after {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1000;
  font-family: 'Technor';
  font-weight: bold;
  font-size: 20vw;
  color: black;
  opacity: 0.2;
  transform: translate(-50%,-50%) rotateZ(-45deg);
  pointer-events: none;  content: "ARCHIVE";
}