.sideBox {
    display: flex;
    align-items: center;
}

.sideBox>input {
    display: block;
    box-sizing: border-box;
    height: 58px;
    border: 2px solid #fff;
    border-radius: 20px;
    outline: none;
    margin-right: 10px;
    background-color: transparent;
    font-family: 'Larsseit';
    padding: 0 25px;
    font-size: 22px;
    color: #fff;
    flex: 1;
    user-select: all !important;
}

.sideBox>input.error {
    border-color: red;
}

.contact-tip {
    display: block;
    font-size: 16px;
    line-height: 1.2em;
    margin: 0.8em 0;
}

/* Celular */
@media (orientation: portrait) {

    .sideBox {
        display: block;
    }

    .sideBox>input {
        margin-top: 15px;
        width: 100%;
    }
}