.aviso-title {
    font-family: 'Technor';
    font-weight: bold;
    font-size: 100px;
    line-height: 1em;
}


.half {
    text-align: left;
    text-justify: auto;
}

/* Celular */
@media (orientation: portrait) {

    .aviso-title {
        font-family: 'Technor';
        font-size: 44px;
        margin-bottom: 0.5em;
    }

}


/* PC / TABLET */
@media (orientation: landscape) {

    .aviso-title {
        white-space: nowrap;
    }

    .half {
        text-align: left;
        text-justify: auto;
        margin-top: 30px;
    }
}