.question {
    display: flex;
    flex-direction: column;
    color: #fff;
    min-height: 100%;
}

.question .hide {
    opacity: 0;
}

.question h1 {
    font-family: 'Technor';
    font-weight: bold;
    font-size: 2.3em;
    text-transform: uppercase;
}

.question ul {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    color: #9037AF;
    font-family: 'Larsseit';
    text-align: center;
}

.question ul>li {
    display: flex;
    flex: 1;
    margin: 10px;
    height: 70px;
    min-width: 40%;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 10px;
    transition-duration: 150ms;
    transition-property: background-color color;
    cursor: pointer;
}

.question ul>li:hover {
    background-color: #612774;
    color: #fff;
}

.question ul>li.rigth {
    transition-duration: 2s;
    background-color: #76d61c;
    color: #fff;
}

.question ul>li.wrong {
    transition-duration: 2s;
    background-color: #d61c1d;
    color: #fff;
}

.question h1 span {
    transition-property: color;
    transition-duration: 2s;
    cursor: pointer;
}

.question span.rigth {
    color: #76d61c;
}

/* PC / TABLET */
@media (orientation: landscape) {
    .question {
        padding: 50px 14vw;
    }

    .question>div {
        display: flex;
        flex: 1;
    }

    .question>div:first-child {
        margin: 0 0 50px 100px;
        display: block;
    }

    .question .lettering {
        margin-right: 50px;
    }
}

/* Celular */
@media (orientation: portrait) {
    .question {
        padding: 10px 10vw;
    }

    .question .lettering {
        font-size: 160px;
        margin-top: 40px;
    }

    .question h1 {
        font-size: 1.3em;
    }

    .question ul>li {
        min-width: 90%;
        margin-left: 0;
        margin-right: 0;
        padding: 20px 10px;
        height: auto;
    }
}