.button {
    display: block;
    position: relative;
    text-align: center;
    background-color: #9037AF;
    text-decoration: none;
    font-family: 'Technor';
    font-weight: bold;
    font-size: 1.2em;
    padding: 15px 25px;
    margin: 50px 5px;
    border-radius: 10px;
    transition-duration: 150ms;
    transition-property: color, background-color;
    color: #ffffff;
    cursor: pointer;
}

.button.loading {
    color: transparent !important;
}

.button:hover {
    background-color: #612774;
}

.button.onDark {
    margin: 20px 0;
    background-color: #fff;
    color: #9037AF;
}

.button.onDark:hover {
    background-color: #612774;
    color: #fff;
}