.lettering {
  display: block;
  white-space: nowrap;
  font-family: 'Technor';
  font-weight: bold;
  font-size: 280px;
  line-height: 0.8em;
  color: transparent;
  -webkit-text-stroke: 2px #fff;
  padding-right: 0.05em;
}

.lettering.special {
  height: 0.4em;
  padding-top: 0.16em;
  line-height: 0.33em;
}

.lettering.face {
  padding-right: 0.1em;
}

/* PC / TABLET */
@media (orientation: landscape) {

  .lettering.special {
    padding-top: 0;
  }

  .lettering.face {
    transform: translateY(-0.4em);
  }

}

/* Celular */
@media (orientation: portrait) {
  .lettering.face {
    font-size: 180px;
    height: 1em;
  }
}