.side {
    display: flex;
    line-height: 1.4em;
    font-weight: lighter;
    min-height: 100%;
}

.side > div:first-child {
    display: flex;
    justify-content: flex-end;
    color: #fff;
    width: 55vw;
    padding: 50px 7vw;
}

.side > div:last-child {
    width: 45vw;
    padding: 5vw;
    background-color: #fff;
    flex: 1;
}

.side > .hideSide {
    z-index: -10;
    background-color: transparent !important;
}

/* Celular */
@media (orientation: portrait) {

    .side {
        flex-direction: column;
    }

    .side>div {
        flex-direction: column;
        width: 100vw !important;
        padding: 10vw !important;
    }

    .side .rigthArea {
        flex: 1;
    }

    .side .content {
        margin-top: 50px;
    }

}

/* PC / TABLET */
@media (orientation: landscape) {

    .side>* {
        height: 100vh;
    }

    .side .leftArea {
        max-width: 500px;
        display: flex;
        flex-direction: column;
    }

    .side .leftArea>div {
        flex: 1;
    }

    .side .rigthArea {
        display: flex;
        align-items: center;
        max-width: 390px;
        height: 100%;
        padding-right: 40px;
        overflow: auto;
    }

    .side .content {
        display: flex;
    }

}